.main_section {
   background: url('../../Images/cafe-background.jpg') no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  z-index: 1;
}
.deliwin {
  font-family: 'Titillium Web', sans-serif;
  font-size: 140px;
  text-shadow: 0px 0px 10px brown;
  cursor: pointer;
  position: absolute;
  top: 0;
}
 .cafe {
  cursor: pointer;
  font-family: 'Titillium Web', sans-serif;
  font-size: 60px;
  text-shadow: 0px 0px 10px brown;
  position:absolute;
  top: 230px;
} 
#start_arrow {
  position: relative;
  font-family: monospace;
}
.deliwin:hover #start_arrow {
  text-shadow: 0 0 10px white;
}


